@font-face {
    font-family: Philosopher;
    src: url(./Assets/Fonts/Philosopher-Regular.ttf);
}

@font-face {
    font-family: PalatinoLinotype;
    src: url(./Assets/Fonts/PalatinoLinotype_Regular.ttf);
}

@font-face {
    font-family: Forum;
    src: url(./Assets/Fonts/Forum.ttf);
}

body {
    background-color: #FAFAFA;
    margin: 0;
}

h1 {
    font-family: Philosopher, 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 3.052rem;
    font-weight: 100;
    text-transform: uppercase;
    margin: 0;
    color: #2b2b2b;
}

h2 {
    font-family: Philosopher, 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.953rem;
    font-weight: 100;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 32px;
    padding: 80px 40px 0 40px;
    color: #252525;
}

p, a {
    font-family: Forum, 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1rem;
    font-weight: 100;
    margin: 0;
    padding: 0 40px 0 40px;
    color: #333333;
}

a {
    text-decoration: none;
}
